import {instance} from "./instance";

export type GetCardsQueryParams = {
    cardsPack_id: string
    cardAnswer?: string
    cardQuestion?: string
    min?: string
    max?: string
    sortCards?: string
    page?: number
    pageCount?: number
}

export type CardType = {
    _id: string
    cardsPack_id: string
    user_id: string
    question: string
    answer: string
    grade: number
    shots: number
    comments?: string
    type?: string
    rating?: number
    more_id?: string
    created: string
    updated: string
    __v?: number
    answerImg?: string
    answerVideo?: string
    questionImg?: string
    questionVideo?: string
}

export type UpdateCardModelType = { _id: string } & Partial<Omit<CardType, "_id">>;

export type GetCardsResponseDataType = {
    cards: CardType[]
    cardsTotalCount: number
    maxGrade: number
    minGrade: number
    page: number
    pageCount: number
    packUserId: string
}

export type NewCardDataType = {
    cardsPack_id: string
    question?: string
    answer?: string
    grade?: number
    shots?: number
    answerImg?: string
    questionImg?: string
    questionVideo?: string
    answerVideo?: string
}

export const cardsApi = {
    getCards(params: GetCardsQueryParams) {
        return instance.get<GetCardsResponseDataType>('cards/card', {params})
            .then(response => response.data)
    },
    createCard(newCards: NewCardDataType) {
        return instance.post('cards/card', {card: newCards})
            .then(response => response.data)
    },
    deleteCard(id: string) {
        return instance.delete('cards/card', {params: {id}})
            .then(response => response.data)
    },
    updateCard(cardModel: UpdateCardModelType) {
        return instance.put('cards/card', {card: cardModel})
            .then(response => response.data);
    },
};